
/**  =========================> *|`| % 费用应收单报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getCostReceivableFormInfo
} from '../../../api/report_forms/cost_receivable';

const config = {
    state: {
        costReceivableFormInfo: {}
    },
    mutations: {
        [types.SAVE_COST_RECEIVABLE_INFO](state, saveInfo) {
            state.costReceivableFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取费用应收单报表信息
        GetCostReceivableFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getCostReceivableFormInfo(queryData).then(response => {
                   if (response.data.status === 1) {
                       commit(types['SAVE_COST_RECEIVABLE_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




