<template>
    <div class="ui-nav-bar">
        <div class="user-info" v-show="!isCollapse">
            <img :src="loginInfo.head_image_url?loginInfo.head_image_url:defaultImg" alt="">
            <p>{{loginInfo.user_name}}</p>
        </div>
        <div class="ui-system-info">
            <span @click="isCollapse = !isCollapse" :style="{ marginRight: isCollapse ? '17px' : '' }">
                <i :class="isCollapse ? 'icon-zhankai' : 'icon-shouqi'" style="color: #666666"></i>
            </span>
        </div>
        <el-menu
                ref="navMenu"
                unique-opened
                :default-active="activeIndex"
                class="ui-menu"
                mode="vertical"
                @select="handleSelectModule"
                :collapse="isCollapse">
            <el-submenu
                    v-for="(item, index) in routerList"
                    :key="index"
                    :index="index + 1 + ''">
                <template slot="title">
                    <i :class="autoActiveParentNav(index + 1) ? 'active ' + item.icon : 'default ' + item.icon "></i>
                    <span :class="autoActiveParentNav(index + 1) ? 'active' : ''">{{item.name}}</span>
                </template>
                <el-menu-item-group
                        v-for="(itemChild, inx) in item.children"
                        :key="inx">
                    <el-menu-item
                            v-if="!itemChild.children || !itemChild.children.length"
                            :index="(index + 1) + '-' + (inx + 1)"><span class="menu-item-dot"><i class="normal-dot" :class="navIndex === (index + 1) + '-' + (inx + 1) ? 'active-dot' : ''"></i></span><span>{{itemChild.name}}</span>
                        <!--<i :class="navIndex === (index + 1) + '-' + (inx + 1) ? 'el-icon-caret-left' : ''" ></i>-->
                    </el-menu-item>
                    <el-submenu
                            v-if="itemChild.children && itemChild.children.length"
                            :index="(index + 1) + '-' + (inx + 1)">
                        <template slot="title"><span class="menu-item-dot"><i class="normal-dot" :class="autoActiveParentNav((index + 1) + '-' + (inx + 1)) ? 'active-dot' : ''"></i></span><span :class="autoActiveParentNav((index + 1) + '-' + (inx + 1)) ? 'active' : ''">{{itemChild.name}}</span>
                        </template>
                        <el-menu-item
                                style="padding-left: 50px!important"
                                v-for="(itemChildren, i) in itemChild.children"
                                :key="i"
                                :index="(index + 1) + '-' + (inx + 1) + '-' + (i + 1)"><span>{{itemChildren.name}}</span>
                            <!--<i :class=" navIndex === (index + 1) + '-' + (inx + 1) + '-' span>{{itemChildren.name}}>{{itemChildren.name}}>{{itemChildren.name}}>{{itemChildren.name}} + 1) ? 'el-icon-caret-left' : ''"></i>-->
                        </el-menu-item>
                    </el-submenu>
                </el-menu-item-group>
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
    import * as storage from '@/utils/storage'
    import { mapGetters, mapActions } from 'vuex';
    import {
        submitCloseAccount,
        submitUnCloseAccount
    } from "@/api/login";
    import routerConfig from '@/router/routerConfig';
    import * as service from '@/services';
    import CONFIG from '@/assets/js/config';

    export default {
        name: "NavPage",
        data() {
            return {
                isShowUpdate: false,
                isCollapse: false,
                activeIndex: '2-1',
                openIndex: '',
                defaultImg: require('../../assets/image/logo-img.png'),
                navFnConfig: {

                    // 关账
                    'close_account': 'handleCloseAccount',

                    // 反关账
                    'un_close_account': 'handleUnCloseAccount'
                },
                closeAccountData: {
                    operator: '',
                    date: ''
                }
            }
        },
        computed: {
            ...mapGetters(['permitPath', 'navIndex','loginInfo']),
            routerList() {
                return routerConfig.getRouteConfig();
            }
        },
        methods: {
            submitCloseAccount,
            submitUnCloseAccount,
            handleSelectModule(inx) {
                let path = routerConfig.navJumpFn(inx).path;
                if (this.navFnConfig[path.replace('/', '')]) {
                    if (this.permitPath.indexOf(path) === -1) {
                        this.$message({
                            type: 'warning',
                            message: '对不起，您没有当前操作的权限！'
                        });
                        return;
                    }

                    this[this.navFnConfig[path.replace('/', '')]]();
                    return;
                }
                this.activeIndex = inx;
                storage.setSessionStorage('navActive', inx);

                let to = routerConfig.navJumpFn(inx);
                this.$router.push(to.path);
                this.$store.commit('SAVE_TAB_LABELS', {
                    path: to.path,
                    name: to.name,
                    index: inx
                });
                this.$store.commit('SAVE_NAV_INDEX', inx)
            },

            // 关账
            handleCloseAccount() {
                this.submitCloseAccount().then(({ data }) => {
                    if (!data.datetime) {
                        this.$message({
                            type: 'warning',
                            message: '暂无可关账账期！'
                        });
                        return;
                    }
                    this.closeAccountData.date = data.datetime.unclose_time;

                    let dateStr = this.closeAccountData.date;
                    let showDate = parseInt(dateStr.substring(dateStr.indexOf('-') + 1, dateStr.indexOf('-') + 3));

                    const h = this.$createElement;
                    this.$msgbox({
                        title: '关账',
                        message: h('p', null, [
                            h('p', null, `确认要对第${showDate}期(${showDate}月数据)进行关账处理吗?`),
                            h('span', null, `关账后，新产生的数据将记录到下期。`)
                        ]),
                        showCancelButton: true,
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                    }).then(action => {
                        let obj = {
                            operator: JSON.parse(storage.getSessionStorage('userInfo')).uid,
                            datetime: this.closeAccountData.date,
                            is_closed: 1
                        };
                        this.submitCloseAccount(obj).then(({ data }) => {
                            if (data.status) {
                                this.$message({
                                    type: 'success',
                                    message: '操作成功'
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    message: data.erorrMess || '操作失败'
                                })
                            }
                        })
                    });
                })
            },

            // 反关账
            handleUnCloseAccount() {

                this.submitUnCloseAccount().then(({ data }) => {
                    if (!data.datetime) {
                        this.$message({
                            type: 'warning',
                            message: '暂无可反关账账期！'
                        });
                        return;
                    }
                    this.closeAccountData.date = data.datetime.close_time;

                    let dateStr = this.closeAccountData.date;
                    let showDate = parseInt(dateStr.substring(dateStr.indexOf('-') + 1, dateStr.indexOf('-') + 3));

                    const h = this.$createElement;
                    this.$msgbox({
                        title: '反关账',
                        message: h('p', null, [
                            h('p', null, `确认要对第${showDate}期(${showDate}月数据)进行反关账处理吗?`),
                            h('span', { style: 'color: red' }, '温馨提示:'),
                            h('span', null, `需确认金蝶系统会计期间也在第${showDate}期。`)
                        ]),
                        showCancelButton: true,
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                    }).then(action => {
                        let obj = {
                            modify_user: JSON.parse(storage.getSessionStorage('userInfo')).uid,
                            datetime: this.closeAccountData.date,
                            is_unclose: 1
                        };
                        this.submitUnCloseAccount(obj).then(({ data }) => {
                            if (data.status) {
                                this.$message({
                                    type: 'success',
                                    message: '操作成功'
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    message: data.errorMess || '操作失败'
                                })
                            }
                        })
                    });
                })
            },

            // 激活父级导航
            autoActiveParentNav(inx) {
                return new RegExp(`^${inx}`).exec(this.navIndex);
            }
        },
        created() {
            let _this=this;
            window.parent.postMessage({
                complete: true
            }, CONFIG.IN_PRODUCTION_PATH);

            this.activeIndex = this.navIndex;
            // 接收上传的头像
            window.addEventListener('message',function(e){
                if(e.data.result){
                    _this.loginInfo.head_image_url=e.data.result;
                    _this.$store.commit('SAVE_LOGIN_INFO',_this.loginInfo);
                }
            });
        },
        mounted() {
            window.addEventListener('message',function(e){
                if (e.data.theme) {
                    let el = document.querySelector('.ui-main');
                    el.className = el.className.replace(/ui-theme[_a-zA-Z]+$/, 'ui-' + e.data.theme);
                }
            });
            let el = document.querySelector('.ui-main');
            el.className = el.className.replace(/ui-theme[_a-zA-Z]+$/, 'ui-' + (this.loginInfo.theme || 'theme_default'));
        },
        watch: {
            isCollapse(val) {
                let view = document.querySelector('.ui-content');
                if (val) {
                    setTimeout(() => {
                        view.style.paddingLeft = '90px';
                    }, 300)
                } else {
                    // this.activeIndex = '1-2';
                    this.$refs.navMenu.updateActiveIndex();
                    view.style.paddingLeft = '220px';
                }
            },
            navIndex(val) {
                this.activeIndex = val;
                this.$refs.navMenu.updateActiveIndex();
            }
        }
    }
</script>

<style scoped lang="less">
    @import (reference) '../../assets/less/index';

</style>



