
/**  =========================> *|`| % 盘盈单 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getInventoryProfitOrderInfo
} from '../../../api/storage_management/inventory_profit_order';

const config = {
    state: {
        inventoryProfitInfo: {}
    },
    mutations: {
        [types.SAVE_INVENTORY_PROFIT_ORDER_INFO](state, saveInfo) {
            state.inventoryProfitInfo = saveInfo;
        }
    },
    actions: {
        // 获取盘盈单信息
        GetInventoryProfitOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getInventoryProfitOrderInfo(queryData).then(response => {
                   if(response.data.status === 1) {
                       commit(types['SAVE_INVENTORY_PROFIT_ORDER_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




