
/**  =========================> *|`| % 销售出库明细报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getSalesFormInfo
} from '../../../api/report_forms/sales_form';

const config = {
    state: {
        salesFormInfo: {}
    },
    mutations: {
        [types.SAVE_SALES_FORM_INFO](state, saveInfo) {
            state.salesFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取销售出库明细报表信息
        GetSalesFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSalesFormInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SALES_FORM_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




