
/**  =========================> *|`| % 采购订单 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPurchaseOrderInfo
} from '../../../api/purchase_management/purchase_order';

const config = {
    state: {
        purchaseOrderInfo: {}
    },
    mutations: {
        [types.SAVE_PURCHASE_ORDER_INFO](state, saveInfo) {
            state.purchaseOrderInfo = saveInfo;
        }
    },
    actions: {
        // 获取采购订单信息
        GetPurchaseOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPurchaseOrderInfo(queryData).then(response => {
                    if(response.data.status === 1){
                        commit(types['SAVE_PURCHASE_ORDER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




