
/**  =========================> *|`| % 采购退款 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPurchaseRefundInfo
} from '../../../api/cashier_management/purchase_refund';

const config = {
    state: {
        purchaseRefundInfo: {}
    },
    mutations: {
        [types.SAVE_PURCHASE_REFUND_INFO](state, saveInfo) {
            state.purchaseRefundInfo = saveInfo;
        }
    },
    actions: {
        // 获取采购退款信息
        GetPurchaseRefundInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPurchaseRefundInfo(queryData).then(response => {
                   if(response.data.status === 1) {
                       commit(types['SAVE_PURCHASE_REFUND_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;





