
/**  --->>>>   |^ 总利润分析|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取总利润信息
export function getTotalProfitAnalysisInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/profit_analysis/profit_report/report' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/profit_analysis/profit_report/report', data);
    }
}


