
/**  --->>>>   |^ 汇总 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取汇总信息
export function getSummaryInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/summary/summary/showsummaryinfolist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/summary/summary/showsummaryinfolist', data);
    }
}

//汇总数据
export function getSummaryData(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/summary/summary/manualsummary', data);
}




