
/**  --->>>>   |^ 汇总错误日志^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取汇总错误日志信息
export function getSumErrorInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/exception_log/exception_log/getsummarizinglog' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/exception_log/exception_log/getsummarizinglog', data);
    }
}


