
/**  --->>>>   |^ 应付单（ 实际 ）^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取应付单（ 实际 ）信息
export function getPayableRealityInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/pay/actual_pay/getactualpayableorder' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/pay/actual_pay/getactualpayableorder', data);
    }
}

// 推送
export function handlePushOrder(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/pay/actual_pay/manualpush', data);
}

// 删除推送
export function delPushOrder(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/pay/actual_pay/deletepush', data);
}


