
/**  =========================> *|`| % 收款退款单 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getRefundOrderInfo
} from '../../../api/cashier_management/refund_order';

const config = {
    state: {
        refundOrderInfo: {}
    },
    mutations: {
        [types.SAVE_REFUND_ORDER_INFO](state, saveInfo) {
            state.refundOrderInfo = saveInfo;
        }
    },
    actions: {
        // 获取收款退款单信息
        GetRefundOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getRefundOrderInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_REFUND_ORDER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




