
/**  =========================> *|`| % 异常处理 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getAbnormalInfo
} from '../../../api/operate_management/operate_abnormal';

const config = {
    state: {
        abnormalInfo: {}
    },
    mutations: {
        [types.SAVE_ABNORMAL_INFO](state, saveInfo) {
            state.abnormalInfo = saveInfo;
        }
    },
    actions: {
        // 获取汇总信息
        GetAbnormalInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getAbnormalInfo(queryData).then(response => {
                    if (response.data.status === 1){
                        commit(types['SAVE_ABNORMAL_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




