
/**  =========================> *|`| % 盘亏单 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getInventoryLossesOrderInfo
} from '../../../api/storage_management/inventory_losses_order';

const config = {
    state: {
        inventoryLossesInfo: {}
    },
    mutations: {
        [types.SAVE_INVENTORY_LOSSES_ORDER_INFO](state, saveInfo) {
            state.inventoryLossesInfo = saveInfo;
        }
    },
    actions: {
        // 获取盘亏单信息
        GetInventoryLossesOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getInventoryLossesOrderInfo(queryData).then(response => {
                   if(response.data.status === 1) {
                       commit(types['SAVE_INVENTORY_LOSSES_ORDER_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




