
/**  --->>>>   |^ 费用明细报表 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取费用明细报表信息
export function getExpensesFormInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/cost/cost_receive/getCostList' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/cost/cost_receive/getCostList', data);
    }
}


// 处理数据
export function flagDisposeInfo(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/cost/cost_receive/updateStatus', data);
}


