
/**  =========================> *|`| % 应收单 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getReceivablesOrderInfo
} from '../../../api/cashier_management/receivables_order';

const config = {
    state: {
        receivablesInfo: {}
    },
    mutations: {
        [types.SAVE_RECEIVABLES_ORDER_INFO](state, saveInfo) {
            state.receivablesInfo = saveInfo;
        }
    },
    actions: {
        // 获取应收单信息
        GetReceivablesOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getReceivablesOrderInfo(queryData).then(response => {
                   if(response.data.status === 1) {
                       commit(types['SAVE_RECEIVABLES_ORDER_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




