<template>
    <div class="ui-main ui-theme_default">
        <!--<div class="ui-nav">-->
            <!--<Navbar></Navbar>-->
        <!--</div>-->
        <div class="ui-content">
            <Main></Main>
        </div>
        <!--<div class="ui-side-bar">-->
        <!--<Sidebar></Sidebar>-->
        <!--</div>-->
        <!--<div class="ui-main-right">-->
        <!--</div>-->
        <Loading></Loading>
    </div>
</template>

<script>
    import {
        Navbar,
        Main
    } from '@/views/HomePage'
    export default {
        name: "Layout",
        components: {
            Navbar,
            Main
        },
        mounted() {
            window.addEventListener('message',function(e){
                if (e.data.theme) {
                    console.log(e.data.theme);
                    let el = document.querySelector('.ui-main');
                    el.className = el.className.replace(/ui-theme[_a-zA-Z]+$/, 'ui-' + e.data.theme);
                }
            });
        }
    }
</script>

<style scoped>

</style>


