
/**  =========================> *|`| % 推送错误日志 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPushErrorInfo
} from '../../../api/error_log/push_error';

const config = {
    state: {
        pushErrorInfo: {}
    },
    mutations: {
        [types.SAVE_PUSH_ERROR_INFO](state, saveInfo) {
            state.pushErrorInfo = saveInfo;
        }
    },
    actions: {
        // 获取推送错误日志信息
        GetPushErrorInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPushErrorInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PUSH_ERROR_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




