
/**  =========================> *|`| % 实际应付单汇总报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPaybleRealityFormInfo
} from '../../../api/report_forms/payble_reality_form';

const config = {
    state: {
        payableRealityFormInfo: {}
    },
    mutations: {
        [types.SAVE_PAYABLE_REALITY_FORM_INFO](state, saveInfo) {
            state.payableRealityFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取实际应付单汇总信息
        GetPaybleRealityFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPaybleRealityFormInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PAYABLE_REALITY_FORM_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




