
/**  --->>>>   |^ 销售出库单 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取销售单信息
export function getSalesOrderInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/sale/sale_out_stock/getsaleoutList' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/sale/sale_out_stock/getsaleoutList', data);
    }
}

// 推送订单
export function handlePushOrder(queryData) {
    if (!queryData) {
        queryData = {};
    }
    return http.postAjax('/sale/sale_out_stock/manualpush', queryData);
}

// 删除推送订单
export function delPushOrder(queryData) {
    if (!queryData) {
        queryData = {};
    }
    return http.postAjax('/sale/sale_out_stock/deletepush', queryData);
}


