
/**  --->>>>   |^ 采购退料 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取采购退料信息
export function getPurchaseReturnInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/purchase/purchase_returns/showpurchasereturnslist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/purchase/purchase_returns/showpurchasereturnslist', data);
    }
}

// 推送订单
export function handlePushOrder(queryData) {
    if (!queryData) {
        queryData = {};
    }
    return http.postAjax('/purchase/purchase_order/manualpurchaseorder', queryData);
}

// 删除推送订单
export function delPushOrder(queryData) {
    if (!queryData) {
        queryData = {};
    }
    return http.postAjax('/purchase/purchase_order/deletepushpurchaseorder', queryData);
}


