
/**  =========================> *|`| % 采购入库 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPurchaseInStockInfo
} from '../../../api/purchase_management/purchase_in_stock';

const config = {
    state: {
        purchaseInStockInfo: {}
    },
    mutations: {
        [types.SAVE_PURCHASE_IN_STOCK_INFO](state, saveInfo) {
            state.purchaseInStockInfo = saveInfo;
        }
    },
    actions: {
        // 获取采购入库信息
        GetPurchaseInStockInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPurchaseInStockInfo(queryData).then(response => {
                    if(response.data.status === 1){
                        commit(types['SAVE_PURCHASE_IN_STOCK_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




