
/**  =========================> *|`| % 费用明细报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getExpensesFormInfo
} from '../../../api/report_forms/expenses_form';

const config = {
    state: {
        expensesFormInfo: {}
    },
    mutations: {
        [types.SAVE_EXPENSES_FORM_INFO](state, saveInfo) {
            state.expensesFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取费用明细报表信息
        GetExpensesFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getExpensesFormInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_EXPENSES_FORM_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




