
/**  --->>>>   |^ 采购运费 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取采购运费信息
export function getPurchaseFreightInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/purchase/purchase_freight/showpurchasefreightlist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/purchase/purchase_freight/showpurchasefreightlist', data);
    }
}

// 推送订单
export function handlePushOrder(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/purchase/purchase_instock/manualpurchaseinstock', data);
}

// 删除推送订单
export function delPushOrder(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/purchase/purchase_instock/deletepushpurchaseinstock', data);
}


