import Vue from 'vue'
import Store from '@/store'
import configFn from './routerConfig'
import Router from 'vue-router'
import LoginPage from '../views/login/Login'
import Layout from '../views/HomePage/Layout'
import SalesVolumeAndSalesAnalyse from '../views/HomePage/ReportForms/SalesVolumeAndSalesAnalyse'
import PlatformSalesAndSalesAnalysis from '../views/HomePage/BusinessAnalysisManagement/PlatformSalesAndSalesAnalysis'
import NotAuthority from '../views/NotAuthority'
import { getUserInfo } from '@/api/login'

Vue.use(Router);

const router =  new Router({
    routes: [
        {
            path: '/login',
            name: 'LoginPage',
            component: LoginPage
        },
        {
            path: '/home',
            name: 'home',
            component: Layout,
            redirect: '/volume_analyse_form',
            children: [
                {
                    path: '/volume_analyse_form',
                    name: '销售额与销量分析表',
                    component: SalesVolumeAndSalesAnalyse
                },

                {
                    path: '/platform_sales_form',
                    name: '平台销售额与销量分析报表',
                    component: PlatformSalesAndSalesAnalysis
                }
            ]
        },
        {
            path: '*',
            redirect: '/home'
        }
    ]
});

export default router;



