<template>
    <div class="ui-login-bg">
        <img src="../../assets/image/login_bg.jpg" alt="">
        <div class="ui-login-wrap">
            <!-- loading  指令 -->
            <!--<v-loading :loading="Loading"></v-loading>-->
            <div class="ui-login-form">
                <h3 class="ui-login-tit">
                    <img src="../../assets/image/login_logo.png" alt="">
                </h3>
                <el-form :model="loginForm" :rules="rules" ref="loginForm" label-width="0" class="demo-ruleForm">
                    <el-form-item prop="username" class="ui-login-input">
                        <el-input type="text"
                                  v-model="loginForm.username"
                                  auto-complete="off"
                                  prefix-icon="icon iconfont el-icon-extra-account"
                                  placeholder="帐号"
                                  @keyup.enter.native="loginSubmit"></el-input>
                    </el-form-item>
                    <el-form-item prop="password" class="ui-login-input">
                        <el-input
                                type="password"
                                v-model="loginForm.password"
                                auto-complete="off"
                                prefix-icon="icon iconfont el-icon-extra-yingckjian"
                                placeholder="密码"
                                @keyup.enter.native="loginSubmit"></el-input>
                    </el-form-item>
                    <div class="button-box">
                        <el-button type="primary" @click="loginSubmit" :loading="loading">登录</el-button>
                    </div>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    import * as storage from '@/utils/storage';
    import {loginByUserName} from '@/api/login'
    import {mapActions} from 'vuex';

    export default {
        name: "login",
        data() {
            return {
                loading: false,
                loginForm: {
                    password: '',
                    username: '',
                    errmsg: ''
                },
                rules: {
                    password: [
                        {
                            required: true,
                            message: '请输入正确的密码',
                            trigger: 'blur'
                        },
                        {
                            min: 3,
                            max: 15,
                            message: '长度在3-15位字符之间',
                            trigger: 'blur'
                        }
                    ],
                    username: [
                        {
                            required: true,
                            message: '请输入正确的账号',
                            trigger: 'blur'
                        },
                        {
                            min: 3,
                            max: 15,
                            message: '长度在3-15位字符之间',
                            trigger: 'blur'
                        }
                    ]
                }
            }
        },
        methods: {
            ...mapActions([
                'LoginByUserName',
                'GetSystemModuleList'
            ]),
            loginByUserName,
            loginSubmit() {
                let vm = this;
                this.$refs['loginForm'].validate(function (res) {
                    if (res) {
                        vm.loading = true;
                        vm.loginForm.errmsg = '';
                        let username = vm.loginForm.username,
                            userpass = vm.loginForm.password;
                        vm.LoginByUserName({
                            user_name: username,
                            pwd: userpass
                        }).then(data => {
                            vm.loading = false;
                            if (data.status) {
                                vm.$router.push('/home');
                            } else {
                                vm.$message({
                                    message: data.errorMess || '操作失败',
                                    type: 'warning'
                                })
                            }
                        }).catch(error => {
                            console.log(error);
                        })
                    } else {
                        vm.loginForm.errmsg = '帐号密码不能为空'
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .ui-login-bg > img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }

    .ui-login {
        width: 300px;
        height: 300px;
        position: absolute;
        top: 400px;
        right: 300px;
    }

    .ui-login-wrap {
        position: absolute;
        left: 50%;
        margin-left: -150px;
        top: 300px;
    }

    .ui-login-form {
        width: 280px;
        /*height: 350px;*/
        background-color: rgba(255, 255, 255, .9);
        border-radius: 8px;
        position: relative;
        top: 32%;
        margin: auto;
        padding-bottom: 35px;
    }

    .ui-login-tit {
        padding: 30px 0;
        text-align: center;
    }

    .ui-login-input {
        padding: 0 20px 0;
    }

    .button-box {
        padding: 0 20px;
    }

    .button-box button {
        width: 100%;
    }
</style>


