
/**  --->>>>   |^ 销售出库汇总报表 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取销售出库汇总报表信息
export function getSalesSumFormInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/report/sale_summary/getsalesummarylist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/report/sale_summary/getsalesummarylist', data);
    }
}

// 获取销售汇总报表明细信息
export function getSalesSumDetailInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    return http.getAjax('/report/sale_summary/getsalesummarydetaillist', data);
}


