
/**  =========================> *|`| % 费用报销报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getExpensesReimburseInfo
} from '../../../api/report_forms/expense_reimbursement';

const config = {
    state: {
        expenseReimburseInfo: {}
    },
    mutations: {
        [types.SAVE_EXPENSES_REIMBURSE_INFO](state, saveInfo) {
            state.expenseReimburseInfo = saveInfo;
        }
    },
    actions: {
        // 获取费用明细报表信息
        GetExpensesReimburseInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getExpensesReimburseInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_EXPENSES_REIMBURSE_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




