
/**  =========================> *|`| % 销售出库汇总报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getSalesSumFormInfo
} from '../../../api/report_forms/sales_sum_form';

const config = {
    state: {
        salesSumFormInfo: {}
    },
    mutations: {
        [types.SAVE_SALES_SUM_FORM_INFO](state, saveInfo) {
            state.salesSumFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取销售出库汇总报表信息
        GetSalesSumFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSalesSumFormInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SALES_SUM_FORM_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




