
/**  =========================> *|`| % 汇总错误日志 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getSumErrorInfo
} from '../../../api/error_log/sum_error';

const config = {
    state: {
        sumErrorInfo: {}
    },
    mutations: {
        [types.SAVE_SUM_ERROR_INFO](state, saveInfo) {
            state.sumErrorInfo = saveInfo;
        }
    },
    actions: {
        // 获取汇总错误日志信息
        GetSumErrorInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSumErrorInfo(queryData).then(response => {
                   if (response.data.status === 1) {
                       commit(types['SAVE_SUM_ERROR_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




