
/**  =========================> *|`| % 收款单 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getReceiptOrderInfo
} from '../../../api/cashier_management/receipt_order';

const config = {
    state: {
        receiptInfo: {}
    },
    mutations: {
        [types.SAVE_RECEIPT_ORDER_INFO](state, saveInfo) {
            state.receiptInfo = saveInfo;
        }
    },
    actions: {
        // 获取收款单信息
        GetReceiptOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getReceiptOrderInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_RECEIPT_ORDER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




