
/**  =========================> *|`| % 汇总 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getSummaryInfo
} from '../../../api/operate_management/operate_sum';

const config = {
    state: {
        summaryInfo: {}
    },
    mutations: {
        [types.SAVE_SUMMARY_INFO](state, saveInfo) {
        
            state.summaryInfo = saveInfo;
            // console.log('summaryInfo',state.summaryInfo)
        }
    },
    actions: {
        // 获取汇总信息
        GetSummaryInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSummaryInfo(queryData).then(response => {
                   if (response.data.status === 1) {
                       commit(types['SAVE_SUMMARY_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




