
/**  =========================> *|`| % 同步错误日志 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getTimingErrorInfo
} from '../../../api/error_log/timing_error';

const config = {
    state: {
        timingErrorInfo: {}
    },
    mutations: {
        [types.SAVE_TIMING_ERROR_INFO](state, saveInfo) {
            state.timingErrorInfo = saveInfo;
        }
    },
    actions: {
        // 获取汇总错误日志信息
        GetTimingErrorInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getTimingErrorInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_TIMING_ERROR_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




