
import { loginByUserName, getUserInfo } from '../api/login';
import * as types from './mutation-type';
import * as storage from '@/utils/storage';
import * as permit from '../api/demo/resource'

export default {
    LoginByUserName ({ commit }, userInfo) {
        return new Promise((resolve, reject) => {
            loginByUserName(userInfo.user_name, userInfo.pwd).then(response => {
                const data = response.data;

            }).catch(error => {
                reject(error);
            });
        });
    },
    GetUserLoginInfo ({ commit }, queryData) {
        return new Promise((resolve, reject) => {
            getUserInfo(queryData).then(({ data }) => {
                commit(types['SAVE_LOGIN_INFO'], data);
                commit(types['SAVE_PERMIT_PATH_INFO'], data.permissions);
                commit(types['SAVE_PERMIT_RESOURCE_INFO'], data.permissions);
                resolve(data);
            }).catch(error => {
                reject(error);
            })
        })
    }
};



