
/**  =========================> *|`| % 采购运费 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPurchaseFreightInfo
} from '../../../api/purchase_management/purchase_freight';

const config = {
    state: {
        purchaseFreightInfo: {}
    },
    mutations: {
        [types.SAVE_PURCHASE_FREIGHT_INFO](state, saveInfo) {
            state.purchaseFreightInfo = saveInfo;
        }
    },
    actions: {
        // 获取采购运费信息
        GetPurchaseFreightInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPurchaseFreightInfo(queryData).then(response => {
                    if(response.data.status === 1){
                        commit(types['SAVE_PURCHASE_FREIGHT_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




