
/**  =========================> *|`| % 预估应付单汇总报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPaybleEstimateFormInfo
} from '../../../api/report_forms/payble_estimate_form';

const config = {
    state: {
        payableEstimateFormInfo: {}
    },
    mutations: {
        [types.SAVE_PAYABLE_ESTIMATE_FORM_INFO](state, saveInfo) {
            state.payableEstimateFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取预估应付单汇总报表信息
        GetPaybleEstimateFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPaybleEstimateFormInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_PAYABLE_ESTIMATE_FORM_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




