
/**  =========================> *|`| % 应付单（实际） % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPayableRealityInfo
} from '../../../api/cashier_management/payable_reality_order';

const config = {
    state: {
        payableRealityInfo: {}
    },
    mutations: {
        [types.SAVE_PAYABLE_REALITY_ORDER_INFO](state, saveInfo) {
            state.payableRealityInfo = saveInfo;
        }
    },
    actions: {
        // 获取应付单（实际）信息
        GetPayableRealityInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPayableRealityInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_PAYABLE_REALITY_ORDER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




