
/**  --->>>>   |^ p卡收款记录^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取p卡收款记录
export function getPayCardInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.is_excel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/financial_records/pcard/getpcardlist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/financial_records/pcard/getpcardlist', data);
    }
}



