
/**  =========================> *|`| % 费用汇总报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getExpensesSummaryFormInfo
} from '../../../api/report_forms/expenses_sum_form';

const config = {
    state: {
        expensesSumFormInfo: {}
    },
    mutations: {
        [types.SAVE_EXPENSES_SUM_FORM_INFO](state, saveInfo) {
            state.expensesSumFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取费用汇总报表信息
        GetExpensesSummaryFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getExpensesSummaryFormInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_EXPENSES_SUM_FORM_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




