
/**  =========================> *|`| % 采购付款 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPurchasePaymentInfo
} from '../../../api/cashier_management/purchase_payment';

const config = {
    state: {
        purchasePaymentInfo: {}
    },
    mutations: {
        [types.SAVE_PURCHASE_PAYMENT_INFO](state, saveInfo) {
            state.purchasePaymentInfo = saveInfo;
        }
    },
    actions: {
        // 获取采购付款信息
        GtPurchasePaymentInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPurchasePaymentInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_PURCHASE_PAYMENT_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




