
/**  --->>>>   |^ 异常处理 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';


//获取异常信息
export function getAbnormalInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/exception_handle/exception_handle/showexceptiondatalist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/exception_handle/exception_handle/showexceptiondatalist', data);
    }
}

//反关账并汇总
export function submitSumAccount(queryData) {
    const data = Object.assign(queryData || {}, CONFIG.ajaxData);
    return http.postAjax('/close_account/close_account/unclosingaccountandsummary', data)
}




