
/**  =========================> *|`| % p卡收款记录 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPayCardInfo
} from '../../../api/cashier_management/payonner_card';

const config = {
    state: {
        payCardInfo: {}
    },
    mutations: {
        [types.SAVE_PAY_CARD_INFO](state, saveInfo) {
            state.payCardInfo = saveInfo;
        }
    },
    actions: {
        // 获取p卡收款记录
        GetPayCardInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPayCardInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_PAY_CARD_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




