
/**  =========================> *|`| % 客户退款明细报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getCustomerFormInfo
} from '../../../api/report_forms/customer_form';

const config = {
    state: {
        customerFormInfo: {}
    },
    mutations: {
        [types.SAVE_CUSTOMER_FORM_INFO](state, saveInfo) {
            state.customerFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取客户退款明细报表信息
        GetCustomerFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getCustomerFormInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_CUSTOMER_FORM_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




