
/**  =========================> *|`| % 采购折扣 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPurchaseDiscountInfo
} from '../../../api/purchase_management/purchase_discount';

const config = {
    state: {
        purchaseDiscountInfo: {}
    },
    mutations: {
        [types.SAVE_PURCHASE_DISCOUNT_INFO](state, saveInfo) {
            state.purchaseDiscountInfo = saveInfo;
        }
    },
    actions: {
        // 获取采购折扣信息
        GetPurchaseDiscountInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPurchaseDiscountInfo(queryData).then(response => {
                  if(response.data.status === 1) {
                      commit(types['SAVE_PURCHASE_DISCOUNT_INFO'], response.data);
                  }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




