<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="报表年份">
                            <el-date-picker
                                    size="small"
                                    v-model="queryData.formDate"
                                    type="year"
                                    format="yyyy 年"
                                    value-format="yyyy"
                                    placeholder="选择年">
                            </el-date-picker>
                        </SearchRequirement>
                    </li>
                    <li class="clearFix">
                        <SearchRequirement label="sku类目">
                            <el-input v-model="queryData.skuClassifyCn" readonly size="small">
                                <template slot="append"><span @click="handleShowDepartment" class="el-icon-search" style="cursor: pointer"></span></template>
                            </el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="采购员">
                            <el-select
                                    filterable
                                    multiple
                                    collapse-tags
                                    v-model="queryData.buyer"
                                    placeholder="请选择"
                                    size="small">
                                <el-option
                                        v-for="(item, index) in optionList.buyerList"
                                        :key="index"
                                        :label="item"
                                        :value="item">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="币种">
                            <el-select
                                    v-model="queryData.currency"
                                    placeholder="请选择"
                                    size="small">
                                <el-option
                                        v-for="(label, value, index) in optionList.currencyList"
                                        :key="index"
                                        :label="label"
                                        :value="value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input v-model="queryData.commodityName" placeholder="可输入关键字进行检索" size="small"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="sku">
                            <el-input v-model="queryData.sku" placeholder="可输入关键字进行检索" size="small"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商">
                            <!--<el-select-->
                                    <!--filterable-->
                                    <!--multiple-->
                                    <!--collapse-tags-->
                                    <!--v-model="queryData.supplier"-->
                                    <!--placeholder="请选择"-->
                                    <!--size="small">-->
                                <!--<el-option-->
                                        <!--v-for="(label, value, key) in optionList.supplierList"-->
                                        <!--:key="key"-->
                                        <!--:label="label"-->
                                        <!--:value="label">-->
                                <!--</el-option>-->
                            <!--</el-select>-->
                            <el-input v-model="queryData.supplier" placeholder="请输入" size="small"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="开发员">
                            <el-select
                                    filterable
                                    multiple
                                    collapse-tags
                                    v-model="queryData.developer"
                                    placeholder="请选择"
                                    size="small">
                                <el-option
                                        v-for="(item, index) in optionList.developerList"
                                        :key="index"
                                        :label="item"
                                        :value="item">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="平台名称">
                            <el-select
                                    filterable
                                    v-model="queryData.platformName"
                                    placeholder="请选择"
                                    size="small">
                                <el-option
                                        v-for="(item, index) in optionList.platformNameList"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="small" @click="handleResetQueryInfo()" >重置</el-button>
                        <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="(item, key) in buttonList"
                    size="small"
                    :type="item.type"
                    :key="key"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <ComTableInfo
                is-summary
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableWidth="tableWidth"
                :tableData="tableData"
                :summary-fn="getSummaries"
                :showSearch="showSearch"
                :span-method="objectSpanMethod"
                @sort-change="handleSort">
            <el-table-column
                    slot="column_classify"
                    :index="autoAddTableInx"
                    type="index"
                    label="序号"
                    width="80">
            </el-table-column>
            <el-table-column
                    slot="column_classify"
                    prop="classify"
                    label="平台名称"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.classify.platform_code}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_classify"
                    prop="classify"
                    label="商品名称"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.classify.picking_name}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_classify"
                    prop="classify"
                    label="sku"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.classify.sku}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    sortable="custom"
                    slot="column_year"
                    label="销售金额"
                    label-class-name="sum_amount"
                    prop="sale_money_year_total"
                    :width="initTableWidth.dataShow">
                <template slot-scope="scope">
                    <span>{{scope.row.year.sum_amount}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    sortable="custom"
                    slot="column_year"
                    label="销售数量"
                    label-class-name="sum_qty"
                    prop="sale_sum_year_total"
                    :width="initTableWidth.dataShow">
                <template slot-scope="scope">
                    <span>{{scope.row.year.sum_qty}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    v-for="(item, index) in tableProps"
                    v-if="index > 1 && index < 14"
                    label="销售金额"
                    sortable="custom"
                    :label-class-name="'price_' + (index - 1)"
                    :slot="'column_' + item"
                    :key="index + 'sum_amount'"
                    :prop="'sale_money_total_' + (index - 1)"
                    :width="initTableWidth.dataShow">
                <template slot-scope="scope">
                    <span>{{scope.row[item].sum_amount}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    v-for="(item, index) in tableProps"
                    v-if="index > 1 && index < 14"
                    label="销售数量"
                    sortable="custom"
                    :label-class-name="'qty_' + (index - 1)"
                    :slot="'column_' + item"
                    :key="index + 'sum_qty'"
                    :prop="'sale_sum_total_' + (index - 1)"
                    :width="initTableWidth.dataShow">
                <template slot-scope="scope">
                    <span>{{scope.row[item].sum_qty}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="一级类目"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.product_onelevel_name}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="二级类目"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.product_twolevel_name}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="三级类目"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.product_threelevel_name}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="创建时间"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.create_date}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="上架时间"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.frame_time}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="首次出单时间"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.first_paytime}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="采购员"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.purchase_person}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="开发员"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.develop_person}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    slot="column_other"
                    prop="classify"
                    label="供应商"
                    :width="initTableWidth.infoShow">
                <template slot-scope="scope">
                    <span>{{scope.row.other.provider_name}}</span>
                </template>
            </el-table-column>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>


        <ComDialogTree
                title="sku类目查询"
                :data="optionList.classifyTreeList"
                :isShow="isShowSkuClassifySel"
                :checked="queryData.skuClassify.category_id"
                :props="skuClassifyProps"
                node-key="category_id"
                @close="handleCloseSearchSkuClassify"
                @confirm="handleConfirmSaveSkuClassify"></ComDialogTree>
    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx,
        changeTableInfoProps
    } from '@/services/index'
    import {
        getPlatformSalesFormInfo,
        getSkuClassifyListInfo
    } from '../../../api/business_analysis_management/platform_sales_form';

    export default {
        data() {
            return {

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                initTableWidth: {
                    dataShow: '120',
                    infoShow: '180'
                },

                //搜索缩进
                showSearch: true,

                optionList: {
                    supplierList:[],
                    currencyList: {},
                    developerList: [],
                    buyerList: [],
                    platformNameList: [],
                    classifyTreeList: []
                },

                // 查询条件
                queryData: {
                    formDate: '',
                    buyer: [],
                    currency: '1',
                    commodityName: '',
                    sku: '',
                    supplier: '',
                    developer: [],
                    sort_type: '',
                    sort_prop: '',
                    platformName: '',
                    skuClassify: {
                        category_id: []
                    },
                    skuClassifyCn: ''
                },

                // 保存默认时间
                saveDefaultDate: '',

                tableKey:[],
                tableData: [],
                tableProps: [],
                tableWidth: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0
                },

                // 保存勾选数据
                saveCheckData: [],
                saveDefaultData: [],

                // 总计
                countData: {},

                // 初始化标记排序
                isFlagSort: false,
                isClearSort: false,

                // 显示搜索sku类目
                isShowSkuClassifySel: false,
                skuClassifyProps: {
                    label: 'category_name',
                    id: 'category_id'
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'platformSalesInfo',
                'loginInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetPlatformSalesFormInfo'
            ]),
            getPlatformSalesFormInfo,
            getSkuClassifyListInfo,
            getDefaultTime,
            autoAddTableInx,
            changeTableInfoProps,
            getSummaries() {
                let sums = [];
                sums[0] = '总计';
                // sums[1] = '总计';
                sums[4] = this.autoCountSumData('sale_money_year_total');
                sums[5] = this.autoCountSumData('sale_sum_year_total');
                sums[6] = this.autoCountSumData('sale_money_total_1');
                sums[7] = this.autoCountSumData('sale_sum_total_1');
                sums[8] = this.autoCountSumData('sale_money_total_2');
                sums[9] = this.autoCountSumData('sale_sum_total_2');
                sums[10] = this.autoCountSumData('sale_money_total_3');
                sums[11] = this.autoCountSumData('sale_sum_total_3');
                sums[12] = this.autoCountSumData('sale_money_total_4');
                sums[13] = this.autoCountSumData('sale_sum_total_4');
                sums[14] = this.autoCountSumData('sale_money_total_5');
                sums[15] = this.autoCountSumData('sale_sum_total_5');
                sums[16] = this.autoCountSumData('sale_money_total_6');
                sums[17] = this.autoCountSumData('sale_sum_total_6');
                sums[18] = this.autoCountSumData('sale_money_total_7');
                sums[19] = this.autoCountSumData('sale_sum_total_7');
                sums[20] = this.autoCountSumData('sale_money_total_8');
                sums[21] = this.autoCountSumData('sale_sum_total_8');
                sums[22] = this.autoCountSumData('sale_money_total_9');
                sums[23] = this.autoCountSumData('sale_sum_total_9');
                sums[24] = this.autoCountSumData('sale_money_total_10');
                sums[25] = this.autoCountSumData('sale_sum_total_10');
                sums[26] = this.autoCountSumData('sale_money_total_11');
                sums[27] = this.autoCountSumData('sale_sum_total_11');
                sums[28] = this.autoCountSumData('sale_money_total_12');
                sums[29] = this.autoCountSumData('sale_sum_total_12');
                return sums;
            },

            // 重置
            handleResetQueryInfo() {
                this.handleFlagSort(this.queryData.sort_type, this.queryData.sort_prop, true);
                this.queryData = {
                    formDate: this.saveDefaultDate,
                    buyer: [],
                    currency: '1',
                    commodityName: '',
                    sku: '',
                    supplier: '',
                    developer: [],
                    platformName: '',
                    skuClassify: {
                        category_id: []
                    },
                    skuClassifyCn: ''
                };
            },

            // 查询
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['platform_sales']);
                }

                let obj = {
                    report_date: this.queryData.formDate,
                    purchaser: this.queryData.buyer.length ? JSON.stringify(this.queryData.buyer) : '',
                    currency: this.queryData.currency,
                    sku_title: this.queryData.commodityName,
                    sku: this.queryData.sku,
                    // supplier_code: this.queryData.supplier.length ? JSON.stringify(this.queryData.supplier): '',
                    supplier_name: this.queryData.supplier,
                    developer: this.queryData.developer.length ? JSON.stringify(this.queryData.developer) : '',
                    sort_field: this.queryData.sort_prop,
                    sort_type: this.queryData.sort_type,
                    platform_code: this.queryData.platformName,
                    product_line_level: JSON.stringify(this.queryData.skuClassify.categoryArr)
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'platform_sales',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetPlatformSalesFormInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 分页
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },

            // 分页
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            //控制可否勾选项
            autoCustomerSelect(row) {
                return row.sign_status === '1';
            },

            // 合并table
            objectSpanMethod({ row, column, rowIndex, columnIndex }) {
                return {
                    rowspan: 1,
                    colspan: 1
                };
            },

            // 手动排序
            handleSort({ column, prop, order }) {
                this.handleFlagSort(this.queryData.sort_type, this.queryData.sort_prop, 'once');
                if (order) {
                    this.queryData.sort_type = order === 'ascending' ? '2' : '1';
                    this.queryData.sort_prop = prop;

                    this.handleQueryInfo({
                        limit: this.pageData.currentList,
                        offset: this.pageData.currentPage
                    });
                } else {
                    this.queryData.sort_type = '';
                    this.queryData.sort_prop = '';

                    this.handleQueryInfo({
                        limit: this.pageData.currentList,
                        offset: this.pageData.currentPage
                    });
                }
            },

            // 标记排序（初始化\重置）
            handleFlagSort(type, prop, clear) {
                if (prop && !this.isFlagSort && !clear) {
                    this.$nextTick(() => {
                        this.isFlagSort = true;
                        let el = document.querySelector(`.${prop}`);
                        if (!el) return;
                        el.className = el.className + (type === '2' ? ' ascending' : ' descending')
                    });
                }
                if (clear && prop) {
                    if (clear === 'once' && this.isClearSort) return;
                    if (clear === 'once') {
                        this.isClearSort = true
                    }

                    let el = document.querySelector(`.${prop}`);
                    if (!el) return;
                    el.className = el.className.replace(/(ascending|descending)/ig, '');
                }
            },

            // 勾选表格数据
            handleSelectionChange(row) {
                this.saveCheckData = row;
            },

            // 导出明细信息
            handleExportListInfo() {
                console.log('123');
                this.handleQueryInfo({
                    isExcel: 1,
                    total: this.pageData.currentTotal
                }, true);
            },

            // 计算总计
            autoCountSumData(key) {
                let arr = this.saveDefaultData;
                let count = 0;
                for (let i = 0; i < arr.length; i++) {
                    count += arr[i][key] * 1;
                }
                return Math.round(count * 100) / 100;
            },

            // 关闭sku类目
            handleCloseSearchSkuClassify() {
                this.isShowSkuClassifySel = false;
            },

            // 保存sku类目信息
            handleConfirmSaveSkuClassify(data) {

                let arr = [];

                for (let i = 0; i < data.id.length; i++) {
                    arr.push({
                        [data.id[i]]: data.level[i]
                    })
                }

                this.queryData.skuClassify = {
                    category_id: data.id,
                    categoryArr: arr
                };

                this.queryData.skuClassifyCn = data.label;
                this.isShowSkuClassifySel = false;
            },

            // 切换下拉框状态
            handleShowDepartment(val) {
                this.isShowSkuClassifySel = true;
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo();
                } else {
                    this.$message.error(data.errorMess || '操作失败')
                }
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                //渲染数据
                this.optionList.currencyList = data.data_list.currency_list;
                // this.optionList.supplierList = data.data_list.supplier_list;
                this.optionList.buyerList = data.data_list.purchaser_list;
                this.optionList.developerList = data.data_list.developer_list;
                this.optionList.platformNameList = data.data_list.platform_code_list;
                this.optionList.classifyTreeList = data.data_list.product_line_level_list;

                let arr = Array.from(data.data_list.value);
                let saveArr = [];
                this.saveDefaultData = arr;
                for (let i = 0; i < arr.length; i++) {
                    saveArr.push({
                        classify: {
                            platform_code: arr[i].platform_code,
                            sku: arr[i].sku,
                            picking_name: arr[i].picking_name
                        },
                        year: {
                            sum_amount: arr[i].sale_money_year_total,
                            sum_qty: arr[i].sale_sum_year_total
                        },
                        month1: {
                            sum_amount: arr[i].sale_money_total_1,
                            sum_qty: arr[i].sale_sum_total_1,
                        },
                        month2: {
                            sum_amount: arr[i].sale_money_total_2,
                            sum_qty: arr[i].sale_sum_total_2,
                        },
                        month3: {
                            sum_amount: arr[i].sale_money_total_3,
                            sum_qty: arr[i].sale_sum_total_3,
                        },
                        month4: {
                            sum_amount: arr[i].sale_money_total_4,
                            sum_qty: arr[i].sale_sum_total_4,
                        },
                        month5: {
                            sum_amount: arr[i].sale_money_total_5,
                            sum_qty: arr[i].sale_sum_total_5,
                        },
                        month6: {
                            sum_amount: arr[i].sale_money_total_6,
                            sum_qty: arr[i].sale_sum_total_6,
                        },
                        month7: {
                            sum_amount: arr[i].sale_money_total_7,
                            sum_qty: arr[i].sale_sum_total_7,
                        },
                        month8: {
                            sum_amount: arr[i].sale_money_total_8,
                            sum_qty: arr[i].sale_sum_total_8,
                        },
                        month9: {
                            sum_amount: arr[i].sale_money_total_9,
                            sum_qty: arr[i].sale_sum_total_9,
                        },
                        month10: {
                            sum_amount: arr[i].sale_money_total_10,
                            sum_qty: arr[i].sale_sum_total_10,
                        },
                        month11: {
                            sum_amount: arr[i].sale_money_total_11,
                            sum_qty: arr[i].sale_sum_total_11,
                        },
                        month12: {
                            sum_amount: arr[i].sale_money_total_12,
                            sum_qty: arr[i].sale_sum_total_12,
                        },
                        other: {
                            product_onelevel_name: arr[i].product_onelevel_name,
                            product_twolevel_name: arr[i].product_twolevel_name,
                            product_threelevel_name: arr[i].product_threelevel_name,
                            purchase_person: arr[i].purchase_person,
                            develop_person: arr[i].develop_person,
                            create_date: arr[i].create_date,
                            frame_time: arr[i].frame_time,
                            first_paytime: arr[i].first_paytime,
                            provider_name: arr[i].provider_name,
                        }
                    })
                }

                this.tableData = Array.from(saveArr);
                this.tableKey = [''].concat(data.data_list.key, '');
                this.tableProps = ['classify', 'year', 'month1', 'month2', 'month3', 'month4', 'month5', 'month6', 'month7', 'month8', 'month9', 'month10', 'month11', 'month12', 'other'];
                this.tableWidth = ['', ''];

                // this.changeTableInfoProps(this.tableData, 'sign_status', 'sign_status_cn', this.optionList.disposeStateList);
                // this.changeTableInfoProps(this.tableData, 'order_type', 'order_type_cn', this.optionList.inOutTypeList);

                //获取分页信息
                this.pageData.currentList = data.page_data.limit * 1 === 0 ? 20 : data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime('Y').then(data => {
                    this.saveDefaultDate = data.end;
                    if (JSON.stringify(this.platformSalesInfo) === '{}') {
                        this.queryData.formDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['platform_sales']);
                        this.handleQueryInfoCallBack(this.platformSalesInfo);
                    }

                    this.handleFlagSort(this.queryData.sort_type, this.queryData.sort_prop);
                });
            } catch (e) {
                console.log(e);
            }
        }
    };
</script>

<style lang="less" scoped>
</style>


