
/**  =========================> *|`| % 应付单（预估） % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPayableEstimateInfo
} from '../../../api/cashier_management/payable_estimate_order';

const config = {
    state: {
        payableEstimateInfo: {}
    },
    mutations: {
        [types.SAVE_PAYABLE_ESTIMATE_ORDER_INFO](state, saveInfo) {
            state.payableEstimateInfo = saveInfo;
        }
    },
    actions: {
        // 获取应付单（预估）信息
        GetPayableEstimateInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPayableEstimateInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_PAYABLE_ESTIMATE_ORDER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




