
/**  =========================> *|`| % 其他出库 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getOtherOutStockInfo
} from '../../../api/storage_management/other_out_stock_order';

const config = {
    state: {
        otherOutStockInfo: {}
    },
    mutations: {
        [types.SAVE_OTHER_OUT_STOCK_INFO](state, saveInfo) {
            state.otherOutStockInfo = saveInfo;
        }
    },
    actions: {
        // 获取其他出库信息
        GetOtherOutStockInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getOtherOutStockInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_OTHER_OUT_STOCK_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




