
/**  --->>>>   |^ 平台销售额与销量分析 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取平台销售额与销量分析报表信息
export function getPlatformSalesFormInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/report/sku_sale_summary_new/salesummaryexport' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/report/sku_sale_summary_new/getsalesummarylist', data);
    }
}

export function getSkuClassifyListInfo(queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/report/sku_sale_summary_new/getsalesummarylist', data);
}


