/**  --->>>>   |^ 预估应付单汇总报表 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取费用应收单报表信息
export function getPaybleEstimateFormInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/report/estimate_pay_list/getestimatepayableorder' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/report/estimate_pay_list/getestimatepayableorder', data);
    }
}


