
/**  =========================> *|`| % 销售订单 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getSalesOrderInfo
} from '../../../api/marketing_management/sales_order';

const config = {
    state: {
        salesOrderInfo: {}
    },
    mutations: {
        [types.SAVE_SALES_OUT_STOCK_ORDER_INFO](state, saveInfo) {
            state.salesOrderInfo = saveInfo;
        }
    },
    actions: {
        // 获取销售订单信息
        GetSalesOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSalesOrderInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_SALES_OUT_STOCK_ORDER_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




