
/**  =========================> *|`| % 采购退料 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPurchaseReturnInfo
} from '../../../api/purchase_management/purchase_return';

const config = {
    state: {
        purchaseReturnInfo: {}
    },
    mutations: {
        [types.SAVE_PURCHASE_RETURN_INFO](state, saveInfo) {
            state.purchaseReturnInfo = saveInfo;
        }
    },
    actions: {
        // 获取采购退料信息
        GetPurchaseReturnInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPurchaseReturnInfo(queryData).then(response => {
                    if(response.data.status === 1){
                        commit(types['SAVE_PURCHASE_RETURN_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




