
/**  =========================> *|`| % 平台销售额与销售分析 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPlatformSalesFormInfo
} from '../../../api/business_analysis_management/platform_sales_form';

const config = {
    state: {
        platformSalesInfo: {}
    },
    mutations: {
        [types.SAVE_PLATFORM_SALES_FORM](state, saveInfo) {
            state.platformSalesInfo = saveInfo;
        }
    },
    actions: {
        // 获取平台利润信息
        GetPlatformSalesFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPlatformSalesFormInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_PLATFORM_SALES_FORM'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




