
/**  =========================> *|`| % 收款汇总报表 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getReceiptSumFormInfo
} from '../../../api/report_forms/receipt_sum_form';

const config = {
    state: {
        receiptSumFormInfo: {}
    },
    mutations: {
        [types.SAVE_RECEIPT_SUM_FORM_INFO](state, saveInfo) {
            state.receiptSumFormInfo = saveInfo;
        }
    },
    actions: {
        // 获取收款汇总报表信息
        GetReceiptSumFormInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getReceiptSumFormInfo(queryData).then(response => {
                   if (response.data.status === 1) {
                       commit(types['SAVE_RECEIPT_SUM_FORM_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




