
import http from '@/http';
import CONFIG from '../../assets/js/config';

export function loginByUserName (userName, password) {
    const data = {
        user_name: userName,
        pwd: password
    };
    return http.postAjax('/login/userlogin', data);
}

export function loginOut (uid) {
    const data = {
        uid: uid
    };
    return http.postAjax('/login/logout', data);
}

// 修改密码
export function updatePassword(obj) {
    const data = {
        uid: obj.uid,
        pwd: obj.oldPwd,
        newpwd: obj.newPwd
    };
    return http.postAjax('/login/updatepwd', data);
}

// 获取用户信息
export function getUserInfo(queryParams) {
    // const data = {};
    return http.getAjax('/login/login/getuserinfo', queryParams);
}

// 关账
export function submitCloseAccount(queryData) {
    const data = Object.assign(queryData || {}, CONFIG.ajaxData);
    return http.getAjax('/close_account/close_account/closingaccount', data);
}

// 反关账
export function submitUnCloseAccount(queryData) {
    const data = Object.assign(queryData || {}, CONFIG.ajaxData);
    return http.postAjax('/close_account/close_account/unclosingaccount', data);
}

//切换主题设置
export function getSetThemeColor(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/single_login/user_data/setTheme', data);
}


