
/**  =========================> *|`| % 平台利润明细 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getPlatformProfitInfo
} from '../../../api/business_analysis_management/platform_profit';

const config = {
    state: {
        platformProfitInfo: {}
    },
    mutations: {
        [types.SAVE_PLATFORM_PROFIT_INFO](state, saveInfo) {
            state.platformProfitInfo = saveInfo;
        }
    },
    actions: {
        // 获取平台利润信息
        GetPlatformProfitInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getPlatformProfitInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_PLATFORM_PROFIT_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




