
/**  =========================> *|`| % 总利润分析 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getTotalProfitAnalysisInfo
} from '../../../api/business_analysis_management/total_profit';

const config = {
    state: {
        totalProfitInfo: {}
    },
    mutations: {
        [types.SAVE_TOTAL_PROFIT_INFO](state, saveInfo) {
            state.totalProfitInfo = saveInfo;
        }
    },
    actions: {
        // 获取总利润信息
        GetTotalProfitAnalysisInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getTotalProfitAnalysisInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_TOTAL_PROFIT_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




