
/**  --->>>>   |^ 应收单 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取应收单信息
export function getReceivablesOrderInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/must_receive/must_receive/getmustreceivelist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/must_receive/must_receive/getmustreceivelist', data);
    }
}


