
/**  --->>>>   |^ 采购付款 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取采购付款信息
export function getPurchasePaymentInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/purchase/purchase_payment/showpurchasepaymentlist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/purchase/purchase_payment/showpurchasepaymentlist', data);
    }
}


