
/**  --->>>>   |^ 收款退款单 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取收款退款单信息
export function getRefundOrderInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/refund_order/refund_order/refundorderlist' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            })
        }))
    } else {
        return http.getAjax('/refund_order/refund_order/refundorderlist', data);
    }
}

// 推送订单
export function handlePushOrder(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/refund_order/refund_order/manualpushrefund', data);
}

// 删除推送订单
export function delPushOrder(queryData) {
    const data = queryData ? Object.assign(queryData, CONFIG.ajaxData) : CONFIG.ajaxData;
    return http.postAjax('/refund_order/refund_order/deletepushrefund', data);
}


