
/**  =========================> *|`| % pingpong收款记录 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getCollectionRecordInfo
} from '../../../api/cashier_management/ping_pong_record';

const config = {
    state: {
        collectionRecordInfo: {}
    },
    mutations: {
        [types.SAVE_PING_PONG_INFO](state, saveInfo) {
            state.collectionRecordInfo = saveInfo;
        }
    },
    actions: {
        // 获取pingpong收款记录
        GetCollectionRecordInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getCollectionRecordInfo(queryData).then(response => {
                    if(response.data.status === 1) {
                        commit(types['SAVE_PING_PONG_INFO'], response.data);
                    }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




