
/**  =========================> *|`| % 销售退货 % |`|*<======================== **/

import * as types from '../../mutation-type';

import {
    getSalesRefundOrderInfo
} from '../../../api/marketing_management/sales_refund_order';

const config = {
    state: {
        salesRefundInfo: {}
    },
    mutations: {
        [types.SAVE_SALES_REFUND_ORDER_INFO](state, saveInfo) {
            state.salesRefundInfo = saveInfo;
        }
    },
    actions: {
        // 获取销售退货信息
        GetSalesRefundOrderInfo({ commit }, queryData) {
            if (!queryData) {
                queryData = {};
            }
            return new Promise((resolve, reject) => {
                getSalesRefundOrderInfo(queryData).then(response => {
                   if(response.data.status === 1){
                       commit(types['SAVE_SALES_REFUND_ORDER_INFO'], response.data);
                   }
                    resolve(response);
                }).catch(error => {
                    reject(error);
                })
            })
        }
    }
};

export default config;




